		.toggle-switch {
			position: relative;
			width: 76px;
			// height: 38px;
			height: 30px;
			background: #f3f3f3;
			border-radius: 24px;
			cursor: pointer;
			transition: background 0.3s;

			@media (max-width: 1536px) {
				width: 45px;
				height: 26px;
			}

			@media (max-width: 1440px) {
				width: 45px;
				height: 25px;
			}

			@media (max-width: 1366px) {
				width: 45px;
				height: 22px;
			}

			@media (max-width: 1280px) {
				width: 40px;
				height: 20px;
			}

			@media (max-width: 768px) {
				width: 35px;
				height: 20px;
			}

			&::before {
				content: "";
				position: absolute;
				// width: 31px;
				// height: 31px;
				width: 22px;
				height: 22px;
				left: 5px;
				top: 3.56px;
				background-color: black;
				border-radius: 50%;
				transition: transform 0.3s;

				@media (max-width: 1536px) {
					width: 20px;
					height: 20px;
					left: 3px;
					top: 3px;
				}

				@media (max-width: 1440px) {
					width: 20px;
					height: 20px;
					left: 3px;
					top: 3px;
				}

				@media (max-width: 1366px) {
					width: 18px;
					height: 18px;
					left: 2px;
					top: 2px;
				}

				@media (max-width: 1280px) {
					width: 16px;
					height: 16px;
					top: 2px;
				}

				@media (max-width: 768px) {
					width: 15px;
					height: 15px;
					left: 3px;
					top: 3px;
				}

				@media (max-width: 430px) {
					width: 16px;
					height: 16px;
					left: 3px;
					top: 2px;
				}
			}

			&.active {
				&::before {
					transform: translateX(45px);

					@media (max-width: 1536px) {
						transform: translateX(19px);
					}

					@media (max-width: 1440px) {
						transform: translateX(20px);
					}

					@media (max-width: 1366px) {
						transform: translateX(23px);
					}

					@media (max-width: 1280px) {
						transform: translateX(19px);
					}

					@media (max-width: 768px) {
						transform: translateX(14px);
					}

					@media (max-width: 430px) {
						transform: translateX(15px);
					}
				}
			}
		}

