	// Hamburger Menu Button
	.hamburger {
		display: flex;
		align-items: center;
		cursor: pointer;
		background: transparent;
		border: 0;
		margin: 0;
		// z-index: 1001;
		height: 50px;
		width: 50px;

		.hamburger-inner {
			background-color: transparent;

			&::before {
				transform: translateY(12px) rotate(45deg);
				background-color: black;
				width: 50px;

				@media screen and (max-width: 430px) {
					transform: translateY(8px) rotate(45deg);
				}

				@media screen and (max-width: 384px) {
					transform: translateY(11px) rotate(45deg);
				}
			}

			&::after {
				transform: translateY(-17px) rotate(-45deg);
				background-color: black;
				width: 50px;

				@media screen and (max-width: 384px) {
					transform: translateY(-14px) rotate(-45deg);
				}
			}
		}
	}

	.hamburger-box {
		width: 50px;
		height: auto;
		display: inline-block;
	}

	.hamburger-inner {
		display: block;
		// top: 50%;
		// margin-top: -1.5px;
		width: 30px;
		height: 5px;
		background-color: black;
		border-radius: 50px;
		position: absolute;
		transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);

		@media screen and (max-width: 430px) {
			width: 24px;
			height: 3px;
		}

		&::before {
			content: "";
			display: block;
			width: 50px;
			height: 5px;
			background-color: black;
			border-radius: 50px;
			position: absolute;
			transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);

			@media screen and (max-width: 430px) {
				width: 40px;
				height: 3px;
			}
		}

		&::after {
			content: "";
			display: block;
			width: 50%;
			height: 5px;
			background-color: black;
			border-radius: 50px;
			position: absolute;
			transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);

			@media screen and (max-width: 430px) {
				width: 50%;
				height: 3px;
			}
		}

		&::before {
			top: -15px;

			@media screen and (max-width: 414px) {
				top: -12px;
			}
		}

		&::after {
			bottom: -15px;
			top: auto;

			@media screen and (max-width: 414px) {
				bottom: -12px;
			}
		}
	}

